.item-type-icon {
  @apply block
    h-4
    pointer-events-none;
    
  margin-top: .15em !important;
  min-width: 16px;
  fill: currentColor;
  transform: rotate(
    360deg
  );
}

.item-type-icon-margin {
  @apply m-inline-end-1.5;
}
